
import { watch, defineComponent, ref } from "vue";
import Widget1 from "@/components/dashboard-default-widgets/Widget1.vue";
import Widget2 from "@/components/dashboard-default-widgets/Widget2.vue";
import Widget3 from "@/components/dashboard-default-widgets/Widget3.vue";
import Widget4 from "@/components/dashboard-default-widgets/Widget4.vue";
import Widget5 from "@/components/dashboard-default-widgets/Widget5.vue";
import Widget6 from "@/components/dashboard-default-widgets/Widget6.vue";
import Widget7 from "@/components/dashboard-default-widgets/Widget7.vue";
import Widget8 from "@/components/dashboard-default-widgets/Widget8.vue";
import Widget9 from "@/components/dashboard-default-widgets/Widget9.vue";
import Widget10 from "@/components/dashboard-default-widgets/Widget10.vue";
import MixedWidget5 from "@/components/widgets/mixed/Widget5.vue";

interface ITargetData {
  liner_decale: number,
  liner_cutting: number,
  liner_gsm: number,
  how_many_liner: number,
  liner_cost: number,

  paper_decale: number,
  paper_cutting: number,
  paper_gsm: number,
  how_many_paper: number,
  paper_cost: number,

  no_of_labour: number,

  percentage_profit: number,

  printing_cost: number,

  with_printing_quantity: number,
  without_printing_quantity: number,
}

interface IDerivedData {
  a: number,
  b: number,
  c: number,

  d: number,
  e: number,
  f: number,

  g: number,

  h: number,

  i: number,
  j: number,

  k: number,
  l: number,
  m: number,
}

export default defineComponent({
  name: "main-dashboard",
  components: {
    Widget1,
    Widget2,
    Widget3,
    Widget4,
    Widget5,
    Widget6,
    Widget7,
    Widget8,
    Widget9,
    Widget10,
    MixedWidget5,
  },
  setup() {
    const targetData = ref<ITargetData>({
      liner_decale: 0,
      liner_cutting: 0,
      liner_gsm: 0,
      how_many_liner: 0,
      liner_cost: 0,

      paper_decale: 0,
      paper_cutting: 0,
      paper_gsm: 0,
      how_many_paper: 0,
      paper_cost: 0,

      no_of_labour: 0,

      percentage_profit: 0,

      printing_cost: 0,

      with_printing_quantity: 0,
      without_printing_quantity: 0,
    });

    const derivedData = ref({
      a: 0,
      b: 0,
      c: 0,

      d: 0,
      e: 0,
      f: 0,

      g: 0,

      h: 0,

      i: 0,
      j: 0,

      k: 0,
      l: 0,
      m: 0,
    });

    watch(targetData.value, (currentValue, oldValue) => {
      console.log(currentValue);
      console.log(oldValue);

      derivedData.value.a = ( ( ( targetData.value.liner_decale * targetData.value.liner_cutting * targetData.value.liner_gsm ) / 1550 ) / 1000 );
      derivedData.value.b = ( derivedData.value.a * targetData.value.how_many_liner );
      derivedData.value.c = ( derivedData.value.b * targetData.value.liner_cost );

      derivedData.value.d = ( ( ( targetData.value.paper_decale * targetData.value.paper_cutting * targetData.value.paper_gsm ) / 1550 ) / 1000 );
      derivedData.value.e = ( derivedData.value.d * targetData.value.how_many_paper );
      derivedData.value.f = ( derivedData.value.e * targetData.value.paper_cost );

      derivedData.value.g = ( ( derivedData.value.b + derivedData.value.e ) * targetData.value.no_of_labour );

      derivedData.value.h = ( derivedData.value.c + derivedData.value.f + derivedData.value.g );

      derivedData.value.i = ( derivedData.value.h * ( targetData.value.percentage_profit / 100 ) );
      derivedData.value.j = ( derivedData.value.h + derivedData.value.i );
      
      derivedData.value.k = ( derivedData.value.j + targetData.value.printing_cost * 1 );
      derivedData.value.l = ( derivedData.value.k * targetData.value.with_printing_quantity );
      derivedData.value.m = ( derivedData.value.j * targetData.value.without_printing_quantity );
    });

    return {
      targetData,
      derivedData
    };
  }
});
