
import { computed, defineComponent, onMounted, ref, watch } from "vue";
import VueApexCharts from "vue3-apexcharts";
import { ApexOptions } from "apexcharts";
import { useStore } from "vuex";
import { getCSSVariableValue } from "@/assets/ts/_utils";

export default defineComponent({
  name: "default-dashboard-widget-6",
  components: {},
  props: {
    className: { type: String, required: false },
    height: { type: String, required: false, default: "425px" },
  },
  setup(props) {
    const chartRef1 = ref<typeof VueApexCharts | null>(null);
    const chartRef2 = ref<typeof VueApexCharts | null>(null);
    let chart: ApexOptions = {};
    const store = useStore();

    const series1 = [
      {
        name: "Social Campaigns",
        data: [[100, 250, 30]],
      },
      {
        name: "Email Newsletter",
        data: [[225, 300, 35]],
      },
      {
        name: "TV Campaign",
        data: [[300, 350, 25]],
      },
      {
        name: "Google Ads",
        data: [[350, 350, 20]],
      },
      {
        name: "Courses",
        data: [[450, 400, 25]],
      },
      {
        name: "Radio",
        data: [[550, 350, 35]],
      },
    ];

    const series2 = [
      {
        name: "Social Campaigns",
        data: [[125, 300, 40]],
      },
      {
        name: "Email Newsletter",
        data: [[250, 350, 35]],
      },
      {
        name: "TV Campaign",
        data: [[350, 450, 30]],
      },
      {
        name: "Google Ads",
        data: [[450, 250, 25]],
      },
      {
        name: "Courses",
        data: [[500, 500, 30]],
      },
      {
        name: "Radio",
        data: [[600, 250, 28]],
      },
    ];

    const themeMode = computed(() => {
      return store.getters.getThemeMode;
    });

    onMounted(() => {
      Object.assign(chart, chartOptions(props.height));

      setTimeout(() => {
        refreshChart();
      }, 200);
    });

    const refreshChart = () => {
      if (!chartRef1.value || !chartRef2.value) {
        return;
      }

      Object.assign(chart, chartOptions(props.height));

      chartRef1.value.refresh();
      chartRef2.value.refresh();
    };

    watch(themeMode, () => {
      refreshChart();
    });

    return {
      chart,
      chartRef1,
      chartRef2,
      series1,
      series2,
    };
  },
});

const chartOptions = (height: string): ApexOptions => {
  const borderColor = getCSSVariableValue("--kt-border-dashed-color");

  return {
    chart: {
      fontFamily: "inherit",
      type: "bubble",
      height: height,
      toolbar: {
        show: false,
      },
    },
    plotOptions: {
      bubble: {},
    },
    stroke: {
      show: false,
      width: 0,
    },
    legend: {
      show: false,
    },
    dataLabels: {
      enabled: false,
    },
    xaxis: {
      type: "numeric",
      tickAmount: 7,
      min: 0,
      max: 700,
      axisBorder: {
        show: false,
      },
      axisTicks: {
        show: true,
        height: 0,
      },
      labels: {
        show: true,
        trim: true,
        style: {
          colors: getCSSVariableValue("--kt-gray-500"),
          fontSize: "13px",
        },
      },
    },
    yaxis: {
      tickAmount: 7,
      min: 0,
      max: 700,
      labels: {
        style: {
          colors: getCSSVariableValue("--kt-gray-500"),
          fontSize: "13px",
        },
      },
    },
    tooltip: {
      style: {
        fontSize: "12px",
      },
      x: {
        formatter: function (val) {
          return "Clicks: " + val;
        },
      },
      y: {
        formatter: function (val) {
          return "$" + val + "K";
        },
      },
      z: {
        title: "Impression: ",
      },
    },
    colors: [
      getCSSVariableValue("--kt-primary"),
      getCSSVariableValue("--kt-success"),
      getCSSVariableValue("--kt-warning"),
      getCSSVariableValue("--kt-danger"),
      getCSSVariableValue("--kt-info"),
      "#43CED7",
    ],
    fill: {
      opacity: 1,
    },
    markers: {
      strokeWidth: 0,
    },
    grid: {
      borderColor: borderColor,
      strokeDashArray: 4,
      padding: {
        right: 20,
      },
      yaxis: {
        lines: {
          show: true,
        },
      },
    },
  };
};
